import React, { Component } from 'react';
import './TodoItem.css';
import { RiMoreFill } from 'react-icons/ri';
import { Link } from 'react-router-dom';

class TodoItem extends Component {

  /* 
  üòØ!
    shouldComponentUpdate
    -> Î≥ÄÍ≤ΩÎêú Î∂ÄÎ∂ÑÎßå Î†åÎçîÎßÅÌïúÎã§.
      nextPropsÎäî thisÏùò propsÏôÄ ÎπÑÍµêÌïòÍ≥†, 
      nextStateÎäî thisÏùò stateÏôÄ ÎπÑÍµêÌïòÏó¨
      Í∞íÏù¥ÎÇò ÏÉÅÌÉúÍ∞Ä Î≥ÄÍ≤ΩÎêú Î∂ÄÎ∂ÑÎßå returnÌïòÏó¨ Î†åÎçîÎßÅÌïúÎã§.
  ü§î?
    nextStateÎäî ÏïàÏì∞ÎäîÎç∞ Ïôú ÏÑ†Ïñ∏ÌñàÏùÑÍπå? ÏÑ†Ïñ∏ÌïòÏßÄ ÏïäÏïÑÎèÑ ÎêòÎÇò? 
  */
  shouldComponentUpdate(nextProps) {
    return this.props.checked !== nextProps.checked;
  }

  /*
  üòØ!
    `todo-text ${ checked && 'checked' }`
    -> checkedÍ∞Ä trueÎ©¥ checkedÎùºÎäî classNameÏùÑ Í∞ÄÏßÑÎã§.

    e.stopPropagation()
    -> eventÏùò Î≤ÑÎ∏îÎßÅ(ÌôïÏÇ∞)ÏùÑ ÎßâÎäîÎã§.
  */
  render() {
    const { text, checked, id, color, onToggle, onRemove } = this.props;

    return (
      <div className="todo-item" onClick={ () => onToggle(id) }>
        <div className="remove" onClick={ (e) => {
          e.stopPropagation();
          onRemove(id)
        } }>&times;</div>

        <div style={ { color } } className={ `todo-text ${ checked && 'checked' }` }>
          <div>{ text }</div>
        </div>

        {
          checked && (<div className="check-mark">&#x2713;</div>)
        }

        <Link className="more" to="./detail">
          <RiMoreFill />
        </Link>
      </div>
    );
  }
}

export default TodoItem;