import React, { Component } from 'react';
import Form from '../components/Form';
import Palette from '../components/Palette';
import TodoItemList from '../components/TodoItemList';
import TodoTemplate from '../components/TodoTemplate';

const colors = ['#000000','#1d3557', '#6b705c', '#ff6b6b'];

class App extends Component {

  /*
  üòØ! 
    idÏùò Ï¥àÍ∏∞Í∞íÏùÑ Ïôú ÏÑ§Ï†ïÌï¥Ï£ºÎäî Í±∏Íπå? 
    -> ÎßàÏßÄÎßâ id Í∞íÏùÑ Ïïå Ïàò ÏóÜÍ∏∞ ÎïåÎ¨∏ÏûÑ
    -> todos.lengthÎ°ú Ìï≠ÏÉÅ ÎßàÏßÄÎßâÏùò id Í∞íÏù¥ ÏÑ∏ÌåÖÎêòÎèÑÎ°ù Ìï®
  */
  // id = 7

  state = {
    input: '',
    todos: [
      { id: 0, text: 'Ìï† Ïùº 1. Redux Ïù¥Ìï¥ Î∞è Ï†ÅÏö©ÌïòÍ∏∞', checked: false },
      { id: 1, text: 'Ìï† Ïùº 2. react-router-dom Ïù¥Ïö©ÌïòÍ∏∞', checked: true },
      { id: 2, text: 'Ìï† Ïùº 3. image upload Í∏∞Îä• Íµ¨ÌòÑÌïòÍ∏∞', checked: false },
      { id: 3, text: 'Ìï† Ïùº 4. color Ï∂îÍ∞Ä Î∞è ÏÇ≠Ï†ú Í∏∞Îä• Íµ¨ÌòÑÌïòÍ∏∞', checked: false },
      { id: 4, text: 'Ìï† Ïùº 5. user id Î∞è password ÏûÖÎ†• form ÎßåÎì§Í∏∞', checked: true },
      { id: 5, 
        text: `Ìï† Ïùº 6. Îëê Ï§ÑÏù¥ ÎÑòÏñ¥Í∞ÄÎ©¥ Ïñ¥ÎñªÍ≤å ÎêòÎäîÏßÄ ÌÖåÏä§Ìä∏Ìï† ÏïÑÏ£º Í∏∏Îã§ÎûóÍ≥† Í∏∏Îã§ÎûÄ Î¨∏Ïû•, 
          ÌÅ¨Í≤å ÎÇòÏÅòÏßÑ ÏïäÏùÄÎç∞ check-markÍ∞Ä toggle Ï†ÑÌôòÎê† ÎïåÎßàÎã§ itemsÏùò widthÍ∞Ä Î≥ÄÌôîÌï®`, 
        checked: true },
      { id: 6, text: 'Ìï† Ïùº 7. GitHub Page ÎßåÎì§Í∏∞', checked: true }
    ],
    color: ''
  }

  // üòØ! input ÌÉúÍ∑∏Ïóê ÏûÖÎ†•Îêú Í∞íÏùÑ stateÏùò inputÏúºÎ°ú ÎÑòÍ∏¥Îã§.
  handleChange = (e) => {
    this.setState({
      input: e.target.value
    });
  }

  /*
  üòØ!
    input: '' 
    -> inputÏùÑ ÎπÑÏö¥Îã§.(Ï¥àÍ∏∞Ìôî)

    Í∑∏Îü¨Î©¥ Îπà Í∞íÏù∏ ÏÉÅÌÉúÎ°ú Î∞∞Ïó¥Ïóê Ï∂îÍ∞ÄÎêòÏßÄ ÏïäÏùÑÍπå? 
    -> Î∞∞Ïó¥Ïóê Ï∂îÍ∞ÄÎêòÎäî Í∞ùÏ≤¥ÏóêÎäî text: inputÏúºÎ°ú stateÏóêÏÑú Í∞íÏùÑ Î∂àÎü¨ÏôÄ ÎÑ£Ïñ¥Ï£ºÍ≥†,
      ÎπÑÏõåÏßÄÎäî inputÏùÄ Ïã§Ï†úÎ°ú ÏÇ¨Ïö©ÏûêÎì§Ïù¥ ÏûÖÎ†•ÌïòÎäî input ÌÉúÍ∑∏Î•º ÎßêÌïúÎã§.
    
    todos.concat(...) 
    -> Í∏∞Ï°¥Ïùò Î∞∞Ïó¥Ïóê ÏÉàÎ°úÏö¥ Í∞ùÏ≤¥Î•º Ï∂îÍ∞ÄÌïú ÏÉàÎ°úÏö¥ Î∞∞Ïó¥ÏùÑ Î∞òÌôòÌïúÎã§.
  */
  handleCreate = () => {
    const { input, todos, color } = this.state;
    this.setState({
      input: '',
      todos: todos.concat({
        id: todos.length,
        text: input,
        checked: false,
        color
      })
    });
  }

  // üòØ! Enter ÌÇ§Î•º ÎàåÎü¨ÎèÑ Create Î≤ÑÌäºÏùÑ ÎàÑÎ•∏ Í≤ÉÍ≥º Í∞ôÏù¥ ÎèôÏûëÌïúÎã§.
  handleKeyPress = (e) => {
    if(e.key === 'Enter') {
      this.handleCreate();
    }
  }

  // ü§î? Ïñ¥ÏßÄÎüΩÎã§.
  handleToggle = (id) => {
    const { todos } = this.state;
    const index = todos.findIndex(todo => todo.id === id);
    const selected = todos[index];
    const nextTodos = [...todos];

    nextTodos[index] = {
      ...selected,
      checked: !selected.checked
    };
    

    this.setState({
      todos: nextTodos
    });
  }

  handleRemove = (id) => {
    const { todos } = this.state;
    this.setState({
      todos: todos.filter(todo => todo.id !== id)
    });
  }

  handleSelectColor = (color) => {
    this.setState({
      color
    })
  }

  render() {
    const { input, todos, color } = this.state;
    const {
      handleChange,
      handleCreate,
      handleKeyPress,
      handleToggle,
      handleRemove,
      handleSelectColor
    } = this;

    return (
      <TodoTemplate 
        form={(
          <Form 
            value={ input }
            onKeyPress={ handleKeyPress }
            onChange={ handleChange }
            onCreate={ handleCreate }
            color={ color } />
        )} 
        items={(
          <TodoItemList 
            todos={ todos }
            onToggle={ handleToggle }
            onRemove={ handleRemove } />
        )}
        palette={(
          <Palette colors={ colors } selected={ color } onSelect={ handleSelectColor } />
        )} />
    );
  }

}

export default App;
